.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}

.justify-content-center {
  justify-content: center !important;
}

.d-flex {
  display: flex !important;
}

.hide {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

@media (max-width: 576px) {
  .hide-for-mobile {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }
}

@media (min-width: 576px) {
  .hide-for-desktop {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }
}

h1 {
  margin-bottom: .5rem;
  font-weight: 500;
  line-height: 1.2;
  font-size: 2.5rem;
  margin-top: 0;
}

.text-center {
  text-align: center !important;
}

.py-3 {
  padding-top: 1rem!important;
  padding-bottom: 1rem!important;
}

.my-5 {
  margin-bottom: 3rem!important;
}

.mt-5, .my-5 {
  margin-top: 3rem!important;
}

.ml-5 {
  margin-left: 3rem;
}

.align-elements {
  margin-right: 10px;
  text-align: right;
  width: 30%;
}

.mb-0 {
  margin-bottom: 0!important;
}

.w-100 {
  width: 100%;
}

.w-50 {
  width: 50%;
}


.table-responsive {
  display: block;
  width: 100%;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;

  .table {
    border-collapse: collapse;
    width: 100%;
    margin-bottom: 1rem;
    color: #212529;

    & tbody tr:hover {
      color: #212529;
      background-color: rgba(0,0,0,.075);
    }

    & thead th {
      vertical-align: bottom;
      border-bottom: 2px solid #dee2e6;
    }

    & tbody tr:nth-of-type(odd) {
      background-color: rgba(0,0,0,.05);
    }

    & td,
    & th {
      border-top: 1px solid #dee2e6;
      padding: .3rem;
      vertical-align: top;
    }
  }
}

input {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

label {
  display: inline-block;
  margin-bottom: .5rem;
}
